.loader_wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    .loader {
      width: 48px;
      height: 48px;
      border: 5px solid #c90202;
      border-bottom-color: transparent;
      border-radius: 50%;
      display: inline-block;
      box-sizing: border-box;
      animation: rotation 1s linear infinite;
      }
  }

  @keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
  } 